@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@font-face{font-family:Futura;src:url(/static/media/17768.7dc41799.ttf)}@font-face{font-family:Rouble;src:url(/static/media/rouble.99ca461a.ttf)}.form-salary{font-family:Futura, sans-serif;font-size:1.3em;margin:2em 3em}.form-salary h3{font-size:0.8em;color:#a6a6a6}.form-salary .inputsBox{margin:1.5em 0}.form-salary .indented{margin-left:calc(1.25rem + 1em + 13px)}.form-salary .form-check label{padding-left:1em}.form-salary .custom-switch-between{display:inline-block;margin-left:1em;margin-right:1em}.form-salary .custom-control-input:checked ~ .custom-control-label::before{border-color:#f0a32f;background:#f0a32f}.form-salary .custom-switch .custom-control-label::before{width:2.5rem;border-radius:1.5rem}.form-salary .custom-control-label::before{height:1.5rem;top:0;border-color:#f0a32f;background:#f0a32f}.form-salary .custom-control-label::after{background-color:#ffffff;top:2px;width:calc(1.5rem - 4px);height:calc(1.5rem - 4px);border-radius:1.5rem}.form-salary .custom-control-input:checked ~ .custom-control-label::after{top:2px;left:calc(-2rem + 2px)}.form-salary .r{font-family:Rouble}.form-salary .tax-computed{margin-top:3em;display:inline-block;padding:2em;background:#fbf4da}.form-salary .tax-computed .money-value{font-weight:bold}

.number-input{margin-right:0.6em;border-radius:1em;outline:0;padding:0.2em 1em 0;width:12em}

.info{display:inline-block;position:absolute}.info .icon{margin-left:0.5em;border:solid 1px #9d9d9d;border-radius:1em;width:1.2em;height:1.2em;color:#9d9d9d;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-family:sans-serif}.info .icon span{font-size:0.8em;vertical-align:top;font-weight:100}.info .banner{margin-top:1.1em;margin-left:1em}.info .banner div:first-child{content:'';position:absolute;z-index:1000;margin-top:-20px;border:10px solid transparent;border-bottom:10px solid #5e6da8;border-left:10px solid #5e6da8}.info .banner div:last-child{z-index:1000;background:#5e6da8;padding:15px;position:relative;color:#fff;font-size:0.8em;max-width:20em}

